import React from 'react';
import NombreUsuario from './NombreUsuario';


const Header = () => {
    const usuario = {nombre:"Ros",apellido:"Rodriguez"};
    return ( <header className="encabezado">
        <div><h1>ComIT</h1></div>
        <div><NombreUsuario user={usuario}/></div>
    </header> );
}
// JSX - JAVASCRIPT Y HTML/XML
 
export default Header;