import React from 'react';


class Reloj extends React.Component{
    constructor(props){
        super(props);
        this.state = {date: new Date()}
    }

    componentDidMount(){
        this.timerID = setInterval(()=>this.clock(), 1000)
    }

    clock(){
        this.setState({date: new Date()})
    }

    render(){
        return(
            <div>
                <h2>La hora es: {this.state.date.toLocaleTimeString()}</h2>
            </div>
        )
    }
}



const Section = () => {
    return ( <section>
        <Reloj/>
        <p>Es una librería de Javascript para crear interfaces de usuario (UI) en el front-end.</p>
        <p>React crea componentes , que son elementos HTML personalizados y reutilizables, para crear interfaces de usuario de manera rápida y eficiente. Además optimiza la forma en que se almacenan y manejan los datos.</p>
        </section> );
}
 
export default Section;